.watermark {
  z-index: 700;
  @include adaptive_value("font-size", 50, 25);
  position: absolute;
  bottom: 1.5%;
  left: 0.4em;
  display: block;
  @include adaptive_value("width", 100, 40);
  @include adaptive_value("height", 50, 20);
  opacity: 0.65;
  transition: all .3s, opacity .5s linear;
  @media only screen and (max-width: 768px) {
    &:active {
      filter: drop-shadow(0px 0px 25px $pink) drop-shadow(0px 0px 7px $pink);
    }
  }
  @media only screen and (min-width: 768px) {
    &:hover {
      filter: drop-shadow(0px 0px 25px $pink) drop-shadow(0px 0px 7px $pink);
    }
  }
  &.hide {
    display: none;
  }
}
